@charset "UTF-8";
body {
  margin: 0;
  padding: 8px;
  padding-bottom: 0;
  font-family: 'Comfortaa';
  background: #eee; }

.component {
  height: calc(100vh - 8px);
  /* фолбэк для браузеров, которые не поддерживают пользовательские свойства */
  height: calc(var(--vh, 1vh) * 100 - 8px); }

.template {
  height: calc(100% - 90px);
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  border: 1px solid black;
  background: #fff; }

.actions {
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center; }

.dot {
  height: 20px;
  width: 20px;
  background: #000;
  border-radius: 50%;
  will-change: transform;
  transition: .7s transform ease, .6s background-color ease;
  position: absolute; }

.device-info {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #eff8fa;
  text-align: center;
  display: none;
  align-items: center;
  padding: 0 5px; }

.btn {
  cursor: pointer; }

@media screen and (max-width: 900px) {
  .template {
    height: calc(100% - 70px); }
  .actions {
    height: 70px; } }

@media screen and (max-width: 750px) {
  .template {
    height: calc(100% - 60px); }
  .actions {
    height: 60px; } }

@media screen and (max-width: 600px) {
  .template {
    height: calc(100% - 50px); }
  .actions {
    height: 50px; } }

@media screen and (max-width: 600px) {
  .btn {
    font-size: 14px; } }

@media screen and (orientation: portrait) {
  .device-info {
    display: flex;
    justify-content: center; } }
